@import "~antd/dist/antd.css";

html {
  font-family: "Source Sans Pro";
}

body {
  font-family: inherit;
  background-color: #f0f2f5;
}

.ant-layout-header {
  background-color: #fff;
  box-shadow: 4px 4px 11px 2px rgba(179, 179, 179, 0.1);
}

.ant-layout-header h1 {
  display: inline-block;
}

.ant-layout-header h1 a {
  text-decoration: none;
  color: #000;
}

.ant-layout-header span {
  color: #ccc;
  float: right;
}

.ant-layout-header span a {
  margin-left: 0.2rem;
}
.ant-layout-content {
  padding: 30px 50px;
}

.ant-card {
  box-shadow: 4px 4px 11px 2px rgba(179, 179, 179, 0.1);
}
